import axios from 'axios';
import React, { useState } from 'react';
import Sidebar from '../Sidebar';
import { Link, useNavigate } from 'react-router-dom'; // Import useNavigate hook
import '../../css/login.css';

const CustomerLogin = () => {
  const apiurl = '/api/login';
  const [input, setInput] = useState({}); // Use an empty object as initial state
  const navigate = useNavigate(); // Initialize useNavigate hook

  const changeinput = (event) => {
    setInput({
      ...input,
      [event.target.name]: event.target.value,
    });
  };

  const buttonclick = (event) => {
    event.preventDefault();
    axios.post(apiurl, input).then((Response) => {
      console.log(Response.data.status);
      if (Response.data.status === 'Login Successful') {
        navigate('/customerdashboard'); // Use navigate function to redirect
      }
    });
  };

  return (
    <div className="d-flex" style={{ padding: '0', margin: '0' }}>
      <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
      <div className="container-fluid d-flex" style={{ justifyContent: 'center', alignContent: 'center' }} >
        <form className="row g-3 px-3 py-3 my-5 d-flex justify-content-center align-items-center " style={{ maxWidth: '400px', margin: '0 auto' }} onSubmit={buttonclick}>
          <h2 className="heading" style={{ marginBottom: '1rem', marginTop: '-1rem' }} >Login</h2>
          <div className="col-12 " style={{ marginTop: '-6rem' }}>
            <label htmlFor="username" className="form-label" >User Name</label>
            <input name="username" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="password" className="form-label">Password</label>
            <input name="password" type="password" className="form-control" onChange={changeinput} />

            <div className="col-12">
              <button className="btn btn-success my-5 " style={{ width: '150px', marginRight: '1rem' }} type="submit">Sign in</button>
              <br />
              <Link to="/customerregister" className="linktosignin" style={{ textDecoration: 'none', textAlign: 'center' }}>Don't have an account? Sign up</Link>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CustomerLogin;
