import React, { useState } from 'react'
import logo from '../images/Logo White.png'
import axios from 'axios'
import SearchResult from './SearchResults'

const Navbar = ({ onSearch }) => {
  const[input, setInput]=useState([])
  const apiurl="/api/viewitem"

  const changeInput=(event)=>{
    
    setInput({
      ...input,[event.target.name]:event.target.value
    })
  }

  const [result, setResult]=useState([])

  const searchbtnclick = () => {
    console.log(input);
    axios.post(apiurl,input).then(
      (Response)=>{
        setResult(Response.data)
        onSearch(Response.data); 
      }
    )
  }

  return (
    <div>
        <nav className="navbar navbar-expand-lg bg-success navbar-dark ">
  <div className="container-fluid">
    <a className="navbar-brand" href="#" >
       <img className="img-fluid rounded" style={{width:'70px'}} src={logo} alt="logo" />
    </a>
    <li className="nav-item" style={{ listStyle: 'none' }}>
         <h3 style={{color:'white', fontStyle:'revert', fontWeight:'lighter'}}>Inventory Management</h3>
        </li>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarScroll" aria-controls="navbarScroll" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarScroll">
      <ul className="navbar-nav me-auto mx-auto my-2 my-lg-0 navbar-nav-scroll text-center align-items-center" style={{'--bs-scroll-height': '10px'}}>
     
        <li className="nav-item" >
          <a className="nav-link active" aria-current="page" href="/dashboard">Home</a>
        </li>
        </ul>
        <ul className="navbar-nav navbar-nav-scroll text-center">
        <li className="nav-item   ">
          <a className="nav-link active" href="/login">
          <i class="fa-solid fa-user"></i>
          </a>
        </li>
      </ul>
     
     
    </div>
  </div>
</nav>
    </div>
  )
}

export default Navbar