import axios from 'axios'
import React, { useState } from 'react'
import Sidebar from '../Sidebar'

const AddItemAdjust = () => {
    const apiurl = '/api/newitemadjust'
    const[input, setInput]=useState([])
  
    const changeInput=(event)=>{
      setInput({
        ...input,[event.target.name]:event.target.value
      })
    }
  
  const buttonclick=()=>{
   
    console.log("Sending data:", input);
    console.log("data:",JSON.stringify(input, null, 2));
  console.log(input);
  axios.post(apiurl,input).then(
    (Response)=>{
      console.log(Response.data);
  
  if (Response.data.status=="success"){
    alert("Item Adjustment added successfully")
  }
  
    }
  )
    }
  
    
    return (   
     
        <div className='d-flex' style={{ padding: '0', margin: '0' }}>
        <div className='container-fluid px-0 mx-0 col-2'> <Sidebar /> </div>
         
          
           <div className='container-fluid px-5 py-3 my-2'>
            <div className="row">
              <div className="col-10">
              <h4 style={{fontWeight:'bold', fontSize:'large'}}>Add Item Adjustment</h4>
              <hr style={{width:'150%', marginLeft:'-3rem'}}/>
  
              <form className="row g-3 my-4 py-3 px-3" style={{width:'120%'}} >
             
              <div className="col-12">
                <div className="form-label">Type</div>
                    <div className="d-flex">
                      <div className="form-check me-4">
                        <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1" />
                        <label className="form-check-label" >Goods</label>
                      </div>
                      <div className="form-check">
                        <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault2" />
                        <label className="form-check-label" >Services</label>
                      </div>
                </div>
              </div>
  
    <div className="col-md-6">
      <label  className="form-label">Mode of Adjust</label>
      <input type="text" name='modeofadjust' className="form-control" onChange={changeInput} id="modeofadjust"/>
      </div>
    
  
    <div className="col-md-6 ">
      <label className="form-label ">Ref No</label>
      <input type="text" name='refnumber' className="form-control " onChange={changeInput} id="refnumber"/>
    </div>
   
    <div className="col-md-4">
      <label className="form-label">Date</label>
      <input type="text" name='date' className="form-control" id="date" onChange={changeInput} placeholder=""/>
    </div>
    <div className="col-md-4">
      <label  className="form-label ">Reason</label>
      <input type="text" name='reason' className="form-control " id="reason" onChange={changeInput} placeholder=""/>
    </div>
    <div className="col-md-4">
      <label className="form-label">Description</label>
      <textarea className="form-control" name='description' aria-label="Description" onChange={changeInput}></textarea>
    </div>
    <div className="col-md-4">
      <label className="form-label">Item Details</label>
      <input type="text" name='itemdetails' className="form-control " onChange={changeInput} id="itemdetails"/>
    </div>
    
    <div className="col-md-4">
    <label  className="form-label">File</label>
    <input className="form-control" name='filepath' type="file" onChange={changeInput} id="formFile"/>
  </div>
  
    <div className="col-12">
      <button type="button" className="btn btn-success my-4 " style={{width:'120px'}} onClick={buttonclick}>Add</button>
    </div>
  </form>
              </div>
            </div>
  
           </div>
  
          </div>
    )
  }

export default AddItemAdjust