import React, { useEffect, useState } from "react";
import { Pie } from "react-chartjs-2";
import axios from "axios";

const InventoryAging = () => {
  const apiurl = '/api/viewitem';
  const [data, setData] = useState([]);

  useEffect(() => {
    axios.post(apiurl)
      .then((Response) => {
        setData(Response.data);
      })
      .catch((error) => {
        console.error('Error:', error);
      });
  }, []);

  // Define age categories and their corresponding ranges
  const ageCategories = [
    { label: "0-30 days", minDays: 0, maxDays: 30 },
    { label: "31-60 days", minDays: 31, maxDays: 60 },
    { label: "61-90 days", minDays: 61, maxDays: 90 },
    // Add more categories as needed
  ];

  // Calculate quantities for each age category
  const categoryQuantities = ageCategories.map(category =>
    data.filter(item =>
      item.daysSinceAcquisition >= category.minDays &&
      item.daysSinceAcquisition <= category.maxDays
    ).length
  );

  const chartData = {
    labels: ageCategories.map(category => category.unit),
    datasets: [
      {
        data: categoryQuantities,
        backgroundColor: [
          "rgb(255, 99, 132)",
          "rgb(54, 162, 235)",
          "rgb(255, 205, 86)",
          // Add more colors if needed
        ],
      },
    ],
  };

  return (
    <div style={{ width: "100%", height: "300px" }}>
      <Pie data={chartData} />
    </div>
  );
};

export default InventoryAging;
