import axios from 'axios';
import React, { useState } from 'react'
import Sidebar from '../Sidebar';

const NewSalesorder = () => {

    const apiurl = '/api/newsalesorder'
    const[input, setInput]=useState({});
   
  
    const changeInput=(event)=>{
      setInput({
        ...input,[event.target.name]:event.target.value
      })
    }
  
  
  const buttonclick=()=>{
    
  
    axios.post(apiurl, input )
     
      .then((Response) => {
        console.log(Response.data);
        if (Response.data.status === 'success') {
          alert('Sales order created successfully');
        }
      })
      .catch((error) => {
        console.error('Error:', error);
        console.log('Request Details:', error.request);
        alert('Sales order creation failed.');
      });
  };
    
    return (   
     
        <div className='d-flex' style={{ padding: '0', margin: '0' }}>
        <div className='container-fluid px-0 mx-0 col-2'> {' '} <Sidebar /> {' '} </div>
         
          
           <div className='container-fluid px-5 py-5 mx-0 d-flex  justify-content-center'>
            <div className="row">
              <div className="col-10">
              <h4 style={{fontWeight:'bold', fontSize:'large'}}>New Sales Order</h4>
              <hr style={{width:'140%', marginLeft:'-3rem'}}/>
  
              <form className="row g-3 my-4 py-5 px-5 " style={{fontSize:'small'}} encType="application/form-data" >
             
              
  
    <div className="col-md-6 col-sm-12">
      <label  className="form-label">Customer Name</label>
      <input type="text" name='customername' className="form-control" onChange={changeInput} id="customername"/>
      </div>
    
  
    <div className="col-md-6 ">
      <label className="form-label ">Sales Order#</label>
      <input type="text" name='salesorder' className="form-control " onChange={changeInput} id="salesorder"/>
    </div>
   
    <div className="col-md-4">
      <label className="form-label">Sales Order Date</label>
      <input type="text" name='salesorderdate' className="form-control " onChange={changeInput} id="salesorderdate"/>
    </div>
    <div className="col-md-4">
      <label  className="form-label ">Expected Shipped Date</label>
      <input type="text" name='expectedshipdate' className="form-control " id="expectedshipdate" onChange={changeInput} placeholder=""/>
    </div>
    <div className="col-md-4 mb-3">
      <label className="form-label">Order Status</label>
      <input type="text" name='orderstatus' className="form-control " onChange={changeInput} id="orderstatus"/>
    </div>
    <div className="col-md-6">
      <label className="form-label">Delivery Method</label>
      <input type="text" name='deliverymethod' className="form-control" id="deliverymethod" onChange={changeInput} placeholder=""/>
    </div>
    <div className="col-md-6 mb-3">
      <label className="form-label">Sales Person</label>
      <input type="text" name='salesperson' className="form-control " onChange={changeInput} id="salesperson"/>
    </div>
    
    
    <div className="col col-12 my-4 d-flex justify-content-center ">
      <button type="button" className="btn btn-success " style={{width:'120px'}} onClick={buttonclick}>Add</button>
    </div>
  </form>
              </div>
            </div>
  
           </div>
  
          </div>
    )
  }

export default NewSalesorder