
import axios from 'axios';
import React, { useState } from 'react'
import Sidebar from '../Sidebar';
import { useNavigate } from 'react-router-dom';

const NewVendorCredit = () => {

    const apiurl = '/api/newvendorcredit'
    const[input, setInput]=useState({});
   const navigate=useNavigate()
  
    const changeInput=(event)=>{
      setInput({
        ...input,[event.target.name]:event.target.value
      })
    }
  
  
  const buttonclick=()=>{
    
  
    axios.post(apiurl, input )
     
      .then((Response) => {
        console.log(Response.data);
        if (Response.data.status === 'success') {
          alert('Vendor Credit Note created successfully');
          navigate('/vendorcreditdisplay')
       
        }
      })
      .catch((error) => {
        console.error('Error:', error);
        console.log('Request Details:', error.request);
        alert('Vendor Credit note creation failed.');
      });
  };
    
    return (   
     
        <div className='d-flex' style={{ padding: '0', margin: '0' }}>
        <div className='container-fluid px-0 mx-0 col-2'> {' '} <Sidebar /> {' '} </div>
         
          
           <div className='container-fluid px-5 py-5 mx-0 d-flex  justify-content-center'>
            <div className="row">
              <div className="col-10">
              <h4 style={{fontWeight:'bold', fontSize:'large'}}>New Vendor Credit Note</h4>
              <hr style={{width:'140%', marginLeft:'-3rem'}}/>
  
              <form className="row g-3 my-4 py-5 px-5 " style={{fontSize:'small'}} encType="application/form-data" >
             
              
  
    <div className="col-md-6 col-sm-12">
      <label  className="form-label">Vendor Credit Note Id</label>
      <input type="text" name='vendorcreditid' className="form-control" onChange={changeInput} id="vendorcreditid"/>
      </div>
  
    <div className="col-md-6 ">
      <label className="form-label ">Vendor</label>
      <input type="text" name='vendor' className="form-control " onChange={changeInput} id="vendor"/>
    </div>

    <div className="col-md-4">
      <label className="form-label">Item</label>
      <input type="text" name='item' className="form-control " onChange={changeInput} id="item"/>
    </div>
   
    <div className="col-md-4">
      <label className="form-label">Date</label>
      <input type="text" name='date' className="form-control " onChange={changeInput} id="date"/>
    </div>

    <div className="col-md-4">
      <label className="form-label">Amount</label>
      <input type="text" name='amount' className="form-control " onChange={changeInput} id="amount"/>
    </div>
    
    
    <div className="col col-12 my-4 d-flex justify-content-center ">
      <button type="button" className="btn btn-success " style={{width:'120px'}} onClick={buttonclick}>Add</button>
    </div>
  </form>
              </div>
            </div>
  
           </div>
  
          </div>
    )
  }

export default NewVendorCredit