import React, { useEffect, useState } from 'react'
import { Navbar } from 'react-bootstrap'
import Sidebar from '../Sidebar'
import { Link, useNavigate } from 'react-router-dom'
import axios from 'axios'

const InventorySummary = () => {

  const apiurl = '/api/viewitem'
  const[data, changedata]=useState([])

  useEffect(()=>{
    axios.post(apiurl).then(
      (Response)=>{
        console.log(Response.data);
        changedata(Response.data)
      }
    )
    .catch((error) => {
      console.error('Error:', error);})
  })  

  return (
   
   
        <div className='d-flex' style={{ padding: '0', margin: '0' }}>
       
        {/* table */}
        <div className='container-fluid px-5 py-3 my-0' >
          <div className="row">
            <div className="col-10">
          
              <div  className='scrolling-table-container' style={{ maxHeight: '200px', width:'750px', overflowY: 'auto' }}>
              <table className="table table-hover" >
              <thead>
                  <tr className='table-active' style={{fontSize:'14px', fontFamily:'sans-serif'}}>
                    <th scope="col" >Name</th>
                    {/* <th scope="col">Group</th>
                    <th scope="col">Description</th> */}
                    <th scope="col">Unit</th>
                    <th scope="col">Brand</th>
                    {/* <th scope="col">Manufacturer</th>
                    <th scope="col">Dimension</th>
                    <th scope="col">Weight</th>
                    <th scope="col">Selling Price</th>
                    <th scope="col">Cost Price</th> */}
                    <th scope="col">Opening Stock</th>
                  </tr>
                    </thead>
                  <tbody style={{fontSize:'14px', fontFamily:'sans-serif'}}>
                    {data.map((item)=>{console.log(item);
                      return  <tr key={item._id}>
                      <td>{item.name}</td>
                      {/* <td>{item.itemgroup}</td>
                      <td>{item.description}</td> */}
                      <td>{item.unit}</td>
                      <td>{item.brand}</td>
                      {/* <td>{item.manufacturer}</td>
                      <td>{item.dimension}</td>
                      <td>{item.weight}</td>
                      <td>{item.sellingprice}</td>
                      <td>{item.costprice}</td> */}
                      <td>{item.openingstock}</td>
                    </tr>
                    })}
                      
                    </tbody>
                  </table>
              </div>
              
              </div> {/* -------col--------- */}
            </div>{/* -------row--------- */}
           </div>{/* -------table container--------- */}
          </div>
    )
  }

export default InventorySummary