import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Sidebar from '../Sidebar'
import { Link } from 'react-router-dom'

const ItemadjustDashboard = () => {
    const apiurl = '/api/viewitemadjust'
    const[data, changedata]=useState([])
  
    useEffect(()=>{
      axios.post(apiurl).then(
        (Response)=>{
          console.log(Response.data);
          changedata(Response.data)
        }
      )
      .catch((error) => {
        console.error('Error:', error);})
    })  
  
    return (
     
      <div  className="d-flex" style={{ padding: '0', margin: '0' }}>
    <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
   <div className="col-12 col-md-10 col-lg-10 col-xl-10 col-xxl-10" >
    
    <h3 style={{textAlign: 'center', marginTop:'2%'}}>Item Adjustments</h3>
    
   <Link to='/additemadjust'>
                <button type="button" className="btn btn-success py-0 px-0  sm-2" style={{marginLeft:'3%', height:'2rem', width:'5rem'}}>+Add</button>
                </Link>
  
   
  <table className="table table-striped border-primary" style={{marginLeft:'3%', width:'1190px'}}>
<thead table-light>
  <tr>
  <th scope="col" >Mode of Adjust</th>
                      <th scope="col">Ref.No</th>
                      <th scope="col">Date</th>
                      <th scope="col">Reason</th>
                      <th scope="col">Description</th>
                      <th scope="col">Item Details</th>
                      <th></th>
                   
    
  </tr>
</thead>
<tbody table-light style={{fontSize:'13px'}}>
  {data.map(
      (item)=>{
          return <tr key={item._id}>
         
           <td>{item.modeofadjust}</td>
                        <td>{item.refnumber}</td>
                        <td>{item.date}</td>
                        <td>{item.reason}</td>
                        <td>{item.description}</td>
                        <td>{item.itemdetails}</td>
            <td>
           
            </td>
          </tr>
          
      }
  )}
  
</tbody>
</table>

   </div>
  
  
  </div>
      )
    }

export default ItemadjustDashboard