import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Sidebar from '../Sidebar'
import '../../css/dashboard.css'
import { Link } from 'react-router-dom'
import Icon from "react-crud-icons";


const DeliveryChallanDisplay = () => {
  
  const apiurl = '/api/deliverychallandisplay'
  const[data, changedata]=useState([])
const navigate=useNavigate()
  

 useEffect(()=>{
  axios.post(apiurl).then(
      (Response)=>{
          console.log(Response.data);
          changedata(Response.data)
      })
  },[])  
  
  
  

 return (
  <div  className="d-flex" style={{ padding: '0', margin: '0' }}>
    <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
   <div className="col-12 col-md-10 col-lg-10 col-xl-10 col-xxl-10" >
    
    <h3 style={{textAlign: 'center', marginTop:'2%'}}>Delivery Challans</h3>
    
   <Link to='/newdeliverychallan'>
                <button type="button" className="btn btn-success py-0 px-0 mt-5 sm-2" style={{marginLeft:'6%', height:'2rem', width:'5rem'}}>+Add</button>
                </Link>
  
   
  <table className="table table-striped border-primary" style={{marginLeft:'6%'}}>
<thead table-light>
  <tr>
    <th scope="col">Customer Name</th>
    <th scope="col">Place of Supply</th>
    <th scope="col">Delivery Challan Id</th>
    <th scope="col">Date</th>
    <th scope="col">Status</th>
    <th scope="col"></th>
    
  </tr>
</thead>
<tbody table-light style={{fontSize:'13px'}}>
  {data.map(
      (item)=>{
          return <tr key={item._id}>
          <td>{item.customername}</td>
            <td>{item.palceofsupply}</td>
            <td>{item.deliverychallanid}</td>
            <td>{item.date}</td>
            <td>{item.status}</td>
            <td>
           
       
            </td>
          </tr>
          
      }
  )}
  
</tbody>
</table>

   </div>
  
  
  </div>
)
}


export default DeliveryChallanDisplay