import React, { useState } from 'react';
import Sidebar from '../Sidebar';
import { Navbar } from 'react-bootstrap';
import axios from 'axios';

const AddItem = () => {

  const apiurl = '/api/newitem'
  const[input, setInput]=useState({});
  const [file, setFile] = useState(null);;

  const changeInput=(event)=>{
    setInput({
      ...input,[event.target.name]:event.target.value
    })
  }

const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };

const buttonclick=()=>{
  const formData = new FormData();
  formData.append('file', file);

  for (const key in input) {
    formData.append(key, input[key]);
  }

  console.log("Sending data:", formData);

  axios.post(apiurl, formData, {
      headers: {
        'Content-Type': 'application/form-data',
      },
    })
    .then((Response) => {
      console.log(Response.data);
      if (Response.data.status === 'success') {
        alert('Item added successfully');
      }
    })
    .catch((error) => {
      console.error('Error:', error);
      console.log('Request Details:', error.request);
      alert('File upload failed.');
    });
};
  
  return (   
   
      <div className='d-flex' style={{ padding: '0', margin: '0' }}>
      <div className='container-fluid px-0 mx-0 col-2'> {' '} <Sidebar /> {' '} </div>
       
        
         <div className='container-fluid px-5 py-3 my-2'>
          <div className="row">
            <div className="col-10">
            <h4 style={{fontWeight:'bold', fontSize:'large'}}>Add Item</h4>
            <hr style={{width:'150%', marginLeft:'-3rem'}}/>

            <form className="row g-3 my-4 py-3 px-3" style={{width:'120%'}} encType="application/form-data" >
           
            <div className="col-12">
              <div className="form-label">Type</div>
                  <div className="d-flex">
                    <div className="form-check me-4">
                      <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1" />
                      <label className="form-check-label" >Goods</label>
                    </div>
                    <div className="form-check">
                      <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault2" />
                      <label className="form-check-label" >Services</label>
                    </div>
              </div>
            </div>
  

  <div className="col-md-6">
    <label  className="form-label">Item Name</label>
    <input type="text" name='name' className="form-control" onChange={changeInput} id="itemname"/>
    </div>
  

  <div className="col-md-6 ">
    <label className="form-label ">Item Group</label>
    <input type="text" name='itemgroup' className="form-control " onChange={changeInput} id="itemgroup"/>
  </div>
 
  <div className="col-md-4">
    <label className="form-label">Description</label>
    <textarea className="form-control" name='description' aria-label="Description" onChange={changeInput}></textarea>
  </div>
  <div className="col-md-4">
    <label  className="form-label ">Unit</label>
    <input type="text" name='unit' className="form-control " id="itemUnit" onChange={changeInput} placeholder=""/>
  </div>
  <div className="col-md-4">
    <label className="form-label">Brand</label>
    <input type="text" name='brand' className="form-control" id="itembrand" onChange={changeInput} placeholder=""/>
  </div>
  <div className="col-md-4">
    <label className="form-label">Manufacturer</label>
    <input type="text" name='manufacturer' className="form-control " onChange={changeInput} id="manufacturer"/>
  </div>
  <div className="col-md-4">
    <label className="form-label">Dimensions</label>
    <input type="text" name='dimension' className="form-control" onChange={changeInput} id="dimensions" placeholder=""/>
  </div>
  <div className="col-md-4">
    <label className="form-label ">Weight</label>
    <input type="text" name='weight' className="form-control " onChange={changeInput} id="weight"/>
  </div>
  <div className="col-md-4">
    <label className="form-label">Selling Price</label>
    <input type="text" name='sellingprice' className="form-control" onChange={changeInput} id="sellingprice" placeholder=""/>
  </div>
  <div className="col-md-4">
    <label  className="form-label ">Cost Price</label>
    <input type="text" name='costprice' className="form-control" onChange={changeInput} id="costprice"/>
  </div>
  <div className="col-md-4">
    <label  className="form-label">Opening Stock</label>
    <input type="text" name='openingstock' className="form-control" onChange={changeInput} id="openingstock" placeholder=""/>
  </div>
  <div className="col-md-4">
    <label  className="form-label ">Preferred Vendor</label>
    <input type="text" name='preferredvendor' className="form-control " onChange={changeInput} id="prefvendor"/>
  </div>
  <div className="col-md-4">
    <label  className="form-label">Reorder Point</label>
    <input type="text" name='reorderpoint' className="form-control" onChange={changeInput} id="reorderpoint"/>
  </div>
  <div className="col-md-4">
                <label className="form-label">Image</label>
                <input
                  className="form-control"
                  name="file"
                  type="file"
                  onChange={handleFileChange}
                  id="formFile"
                />
              </div>

  <div className="col-12">
    <button type="button" className="btn btn-success my-4 " style={{width:'120px'}} onClick={buttonclick}>Add</button>
  </div>
</form>
            </div>
          </div>

         </div>

        </div>
  )
}

export default AddItem