import axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'

const Login = () => {
const navigate=useNavigate()
const apiurl="/api/userlogin"
const [input,setInput]=useState([])
const changeinput=(event)=>{
    setInput({
        ...input,[event.target.name]:event.target.value
    })
}
    const btnclick=()=>{
        axios.post(apiurl,input).then(
            (Response)=>{
                console.log(Response.data.status);
                if (Response.data.status=="Login Successful"){
                    navigate('/dashboard')
                    
                } else {
                    alert ("Invalid credentials")
                }
            }
        )
    }
  return (
    <div className='background-image'>       
<div className="container px-5 py-5" style={{marginTop:"6%", width:'40%'}}>

    <div className="row g-3">
        <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12">
            <h2 className='heading'>User Login</h2>
            <p className='section mb-5'  style={{fontSize:'14px'}}>to continue</p>
            <label htmlFor="" className="form-label">Username</label>
            <input name ="username"  type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="" className="form-label">Password</label>
            <input name ="password"  type="password" className="form-control" onChange={changeinput} />
            <button class="btn btn-success mx-3 my-5" type="submit" onClick={btnclick}>Sign in</button>
            <Link to = '/newuser' className='linktosignin' style={{textDecoration:'none', color:'green'}}>Don't have an account? Sign up</Link>
       
            </div> 
    </div>
</div>




    </div>
  )
}

export default Login