import React, { useEffect, useState } from "react";
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";
import { Pie } from "react-chartjs-2";
import axios from "axios";



const SalesReport = () => {

    const apiurl = '/api/invoicesdisplay'
    const[data, changedata]=useState(0)
    useEffect(()=>{
        axios.post(apiurl).then(
          (Response)=>{
            console.log(Response.data.invoicecount);
            changedata(Response.data.invoicecount)
          }
        )
        .catch((error) => {
          console.error('Error:', error);})
      },[])  
   
      const apiurl1 = '/api/packagedisplay'
      const[packagedata, changepackagedata]=useState(0)
      useEffect(()=>{
          axios.post(apiurl1).then(
            (Response)=>{
              console.log(Response.data.packagecount);
              changedata(Response.data.packagecount)
            }, []
          )
          .catch((error) => {
            console.error('Error:', error);})
        },[])  
    return (
        <table className="table table-hover" >
              <thead>
                  <tr className='table-active' style={{fontSize:'14px', fontFamily:'sans-serif'}}>
                    <th scope="col" >Invoiced</th>
                    <th scope="col">Packed</th>
                   
                  </tr>
                    </thead>
                  <tbody style={{fontSize:'14px', fontFamily:'sans-serif'}}>
                
                   
                      <tr>
                      <td>{data}</td>
                      <td>{packagedata}</td>
                      
                    </tr>
                   
                      
                    </tbody>
                  </table>
      );
};

export default SalesReport;