import React from 'react'
import Sidebar from '../Sidebar'
import ItemChart from './ItemChart';
import InventoryAging from './InventoryAging';
import SalesReport from './SalesReport';
import SalesbyCust from './SalesbyCust';
import InventorySummary from './InventorySummary';
import Navbar from '../Navbar';
import SearchResult from '../SearchResults';
import { useState } from 'react';

const Dashboard = () => {
  const [filteredData, setFilteredData] = useState([]); // State for filtered data

  const handleSearch = (filteredResults) => {
    setFilteredData(filteredResults); // Update the filtered data state
  };

  return (
    <div className='d-flex' style={{ padding: '0', margin: '0' }}>
    <div className='container-fluid px-0 mx-3 col-2 '>
       <Sidebar /> 
    </div>
 

    <div className="row row-cols-1 row-cols-md-3 px-3 py-4 align-items-center justify-contents-center">
    <div className="row">

    {/* {filteredData.length > 0 && <SearchResult filteredResults={filteredData} />} */}

          <div className="col  mb-3">
            <div className="card" >
              <div className="card-body">
                <h5 className="card-title">Inventory Details</h5>
                <ItemChart />
              </div>
            </div>
          </div>
        </div>

    <div className="col  mb-3">
      <div className="card">
        <div className="card-body">
          <h5 className="card-title">Inventory Aging Summary</h5>
          <InventoryAging />
        </div>
      </div>
    </div>
    
    <div className="col  mb-3">
      <div className="card">
        <div className="card-body">
          <h5 className="card-title">Sales By Customer</h5>
         <SalesbyCust/>
        </div>
      </div>
    </div>

    <div className="col">
      <div className="card" style={{width:'360px'}}>
        <div className="card-body">
          <h5 className="card-title">Sales Report</h5>
          <SalesReport/>
        </div>
      </div>
    </div>

    <div className="col">
      <div className="card"  style={{width:'750px'}}>
        <div className="card-body">
          <h5 className="card-title">Inventory Summary</h5>
          <InventorySummary />
        </div>
      </div>
    </div>

   

  </div>
  </div>
  )
}

export default Dashboard