import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import Sidebar from '../Sidebar'

const EditCustomer = () => {
    const navigate=useNavigate()
    const {id}=useParams();
    const apiurl = '/api/editvendor/'+id
      
    const[input, setInput]=useState([])
    const[result, setResult]=useState([])
  
    const apiurl1 = '/api/vendordashboard'
    const[data, changedata]=useState([])
  
    // const navigate=useNavigate()
    
  
   useEffect(()=>{
    axios.post(apiurl1).then(
        (Response)=>{
            console.log(Response.data);
            changedata(Response.data)
        })
    },[])  
  
  
    const changeinput=(event)=>{    
      setInput({
        ...input,[event.target.name]:event.target.value
      })
    }
  
  
  const buttonclick=()=>{
  console.log(input);
  axios.put(apiurl,input).then(
    (Response)=>{
      console.log(Response.data);
     setResult(Response.data)
   
    //   navigate('/customerdashboard');
  
  if (Response.data.status==="success"){
    alert("Customer data updated successfully")
  }
  
    }
  )
    }
    return (
      <div className="d-flex" style={{ padding: '0', margin: '0' }}>
       <div className="container-fluid px-0 mx-0 col-2  "> {' '} <Sidebar /> {' '} </div>
      <div className="container-fluid ps-0 mx-sm-0">
          <div className="row g-3">
            
                <div className="col col-12 col-sm-12 col-md-12 col-lg-9 col-xl-9 col-xxl-9 mx-sm-0 ">
     
      <div className="mycontainer ">
      {data.map((item)=>{
         if (item._id === id) {
  
              return <div className="row g-3">
              <br />
              <br />
           <form action="" className="row g-3  py-3 my-5  mx-sm-0 d-flex justify-content-center align-items-center">
           <h5 className='formheading py-3'>Update Customer</h5>
              <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 mx-sm-0 ">
              <label htmlFor="firstname" className="form-label">First Name</label>
            <input name="firstname" type="text" placeholder={item.firstname} className="form-control" onChange={changeinput} />
            <label htmlFor="lastname" className="form-label">Last Name</label>
            <input name="lastname" type="text" placeholder={item.lastname} className="form-control" onChange={changeinput} />
            <label htmlFor="city" className="form-label">City</label>
            <input name="city" type="text" className="form-control" placeholder={item.city} onChange={changeinput} />
            <label htmlFor="zip" className="form-label">Zip</label>
            <input name="zip" type="text" className="form-control" placeholder={item.zip} onChange={changeinput} />
            {/* <label htmlFor="username" className="form-label">User Name</label>
            <input name="username" type="text" className="form-control" placeholder={item.username} onChange={changeinput} />
            <label htmlFor="password" className="form-label">Password</label>
            <input name="password" type="password" className="form-control" placeholder={item.password} onChange={changeinput} /> */}
              <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 py-5 d-flex justify-content-center align-items-center">
                 <button type='submit' className='btn btn-primary' onClick={buttonclick}>Update</button>
              </div>
              </div>
            
           </form>
             
          </div>
         }
            })}
              
             
          </div>
          <section></section>
  
  <section></section>
  </div>
  </div>
  </div>
  </div>
 
 
    )
  }
  
  

export default EditCustomer;