
import React, { useState } from 'react';
import Sidebar from '../Sidebar';
import axios from 'axios';
import '../../css/register.css';
import { Link } from 'react-router-dom';

const NewVendor = () => {
  const initialState = {
    firstname: '',
    lastname: '',
    city: '',
    zip: '',
    username: '',
    password: '',
  };

  const apiurl = '/api/newvendor';
  const [input, setInput] = useState(initialState);

  const changeinput = (event) => {
    setInput({
      ...input,
      [event.target.name]: event.target.value,
    });
  };

  const buttonclick = (event) => {
    event.preventDefault();
    console.log(input);
    axios
      .post(apiurl, input)
      .then((Response) => {
        console.log('Response:', Response.data);

        if (Response.data.status === 'success') {
          alert('Successfully added vendor');
        }
      })
      .catch((error) => {
        console.error('Axios error:', error);
        // Handle the error, display a message, or perform other actions
      });
  };

  return (
    <div className="d-flex" style={{ padding: '0', margin: '0' }}>
      <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
      <div className="container-fluid d-flex" style={{ justifyContent: 'center', alignContent: 'center' }} >
        <form className="row g-3 px-3 py-3 my-5 d-flex justify-content-center align-items-center " >
          <h2 className="heading">Add Vendor</h2>
          <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12  ">
            <label htmlFor="firstname" className="form-label">First Name</label>
            <input name="firstname" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="lastname" className="form-label">Last Name</label>
            <input name="lastname" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="city" className="form-label">City</label>
            <input name="city" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="zip" className="form-label">Zip</label>
            <input name="zip" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="phoneno" className="form-label">Phone No</label>
            <input name="phoneno" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="email" className="form-label">Email</label>
            <input name="email" type="email" className="form-control" onChange={changeinput} />

            <div className="col-12">
              <button className="btn btn-success my-5 " style={{ width: '150px', marginRight: '1rem' }} onClick={buttonclick} type="submit">Add</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NewVendor;
