import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Sidebar from '../Sidebar'
import '../../css/dashboard.css'
import { Link } from 'react-router-dom'
import Icon from "react-crud-icons";


const CustomerDashboard = () => {
  
  const apiurl = '/api/customerdashboard'
  const[data, changedata]=useState([])
const navigate=useNavigate()
  

 useEffect(()=>{
  axios.post(apiurl).then(
      (Response)=>{
          console.log(Response.data);
          changedata(Response.data)
      })
  },[])  
  
  
  const deletebtn=async(id)=>{
    try {
      axios.delete('/api/deletecustomer/'+id)
      window.location.reload()
    
      
    } catch (error) {
      console.log(error);
    }
        }

 return (
  <div  className="d-flex" style={{ padding: '0', margin: '0' }}>
    <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
   <div className="col-12 col-md-10 col-lg-10 col-xl-10 col-xxl-10" >
   <h3 style={{textAlign: 'center', marginTop:'2%'}}>Customer Data</h3>
   <Link to='/customerlogin'>
                <button type="button" className="btn btn-success py-0 px-0 mt-5 sm-2" style={{marginLeft:'6%', height:'2rem', width:'5rem'}}>Login</button>
                </Link>

  <table className="table table-striped border-primary" style={{marginLeft:'6%'}}>
<thead table-light>
  <tr>
    <th scope="col">First Name</th>
    <th scope="col">Last Name</th>
    <th scope="col">City</th>
    <th scope="col">Zip</th>
    <th scope="col"></th>
    
  </tr>
</thead>
<tbody table-light>
  {data.map(
      (item)=>{
          return <tr key={item._id}>
          <td>{item.firstname}</td>
            <td>{item.lastname}</td>
            <td>{item.city}</td>
            <td>{item.zip}</td>
            <td>
            <Link to={`/updatecustomer/${item._id}`} className='icon-link'>
                <Icon name = "edit" tooltip = ""  theme = "light"  size = "small" style={{ cursor: 'pointer' }} />
                </Link>
       
                <Link to={``} className='icon-link-del'>
                <Icon name = "delete" tooltip = ""  theme = "light"  size = "small" style={{ cursor: 'pointer' }} onClick={ e => deletebtn(item._id)} />
                </Link>
            </td>
          </tr>
          
      }
  )}
  
</tbody>
</table>

   </div>
  
  
  </div>
)
}


export default CustomerDashboard