import React, { useEffect, useState } from "react";
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";
import { Pie } from "react-chartjs-2";
import axios from "axios";



const ItemChart = () => {

    const apiurl = '/api/viewitemgroup'
    const[data, changedata]=useState([])
    useEffect(()=>{
        axios.post(apiurl).then(
          (Response)=>{
            console.log(Response.data);
            changedata(Response.data)
          }
        )
        .catch((error) => {
          console.error('Error:', error);})
      },[])  
   
    const chartdata = {
      labels: ["Itemgroups"],
      datasets: [
        {
            label: data.map(item => item.name),
            data: data.map(item => item.unit), // Extracting corresponding quantities
            backgroundColor: ["rgb(255, 99, 132)", "rgb(54, 162, 235)", "rgb(255, 205, 86)"], // Customize colors as needed
          },
      ],
    };
    const chartOptions = {
        plugins: {
          legend: {
            display: true,
            position: "bottom",
          },
        },
      };
    return (
        <div style={{ width: "150%", height: "300px"}} >
          <Pie data={chartdata} options={chartOptions} />
        </div>
      );
};

export default ItemChart;