
import React, { useState } from 'react';
import Sidebar from '../Sidebar';
import axios from 'axios';
import '../../css/register.css';
import { useNavigate } from 'react-router-dom';

const NewPurchaseOrder = () => {
  const initialState = {
    firstname: '',
    lastname: '',
    city: '',
    zip: '',
    username: '',
    password: '',
  };

  const apiurl = '/api/newpurchaseorder';
  const [input, setInput] = useState(initialState);
const navigate=useNavigate()
  const changeinput = (event) => {
    setInput({
      ...input,
      [event.target.name]: event.target.value,
    });
  };

  const buttonclick = (event) => {
    event.preventDefault();
    console.log(input);
    axios
      .post(apiurl, input)
      .then((Response) => {
        console.log('Response:', Response.data);

        if (Response.data.status === 'success') {
          alert('Successfully created purchase order');
          navigate('/purchaseorderdisplay')
        }
      })
      .catch((error) => {
        console.error('Axios error:', error);
        // Handle the error, display a message, or perform other actions
      });
     
  };

  return (
    <div className="d-flex" style={{ padding: '0', margin: '0' }}>
      <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
      <div className="container-fluid d-flex" style={{ justifyContent: 'center', alignContent: 'center' }} >
        <form className="row g-3 px-3 py-3 my-5 d-flex justify-content-center align-items-center " >
          <h2 className="heading">Add Purchase Order</h2>
          <div className="col col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12  ">
            <label htmlFor="vendorname" className="form-label">Vendor Name</label>
            <input name="vendorname" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="deliveryaddress" className="form-label">Delivery Address</label>
            <input name="deliveryaddress" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="purchaseorderno" className="form-label">Purchase Order No</label>
            <input name="purchaseorderno" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="date" className="form-label">Date</label>
            <input name="date" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="duedate" className="form-label">Due Date</label>
            <input name="duedate" type="text" className="form-control" onChange={changeinput} />
            <label htmlFor="status" className="form-label">Status</label>
            <input name="status" type="text" className="form-control" onChange={changeinput} />
          

            <div className="col-12">
              <button className="btn btn-success my-5 " style={{ width: '150px', marginRight: '1rem' }} onClick={buttonclick} type="submit">Add</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NewPurchaseOrder;
