import React, { useEffect, useState } from 'react'
import { Navbar } from 'react-bootstrap'
import Sidebar from '../Sidebar'
import { Link, useNavigate } from 'react-router-dom'
import axios from 'axios'

const Itemdashboard = () => {

  const apiurl = '/api/viewitem'
  const[data, changedata]=useState([])

  useEffect(()=>{
    axios.post(apiurl).then(
      (Response)=>{
        console.log(Response.data);
        changedata(Response.data)
      }
    )
    .catch((error) => {
      console.error('Error:', error);})
  })  

  return (
    <div  className="d-flex" style={{ padding: '0', margin: '0' }}>
    <div className="container-fluid px-0 mx-0 col-2 "> {' '} <Sidebar /> {' '} </div>
   <div className="col-12 col-md-10 col-lg-10 col-xl-10 col-xxl-10" >
    
    <h3 style={{textAlign: 'center', marginTop:'2%'}}>Item Details</h3>
    
   <Link to='/additem'>
                <button type="button" className="btn btn-success py-0 px-0  sm-2" style={{marginLeft:'3%', height:'2rem', width:'5rem'}}>+Add</button>
                </Link>
  
   
  <table className="table table-striped border-primary" style={{marginLeft:'3%', width:'1190px'}}>
<thead table-light>
  <tr>
  <th scope="col" >Name</th>
                    <th scope="col">Group</th>
                    <th scope="col">Description</th>
                    <th scope="col">Unit</th>
                    <th scope="col">Brand</th>
                    <th scope="col">Manufacturer</th>
                    <th scope="col">Dimension</th>
                    <th scope="col">Weight</th>
                    <th scope="col">Selling Price</th>
                    <th scope="col">Cost Price</th>
                    <th scope="col">Opening Stock</th>
                    <th></th>
    
  </tr>
</thead>
<tbody table-light style={{fontSize:'13px'}}>
  {data.map(
      (item)=>{
          return <tr key={item._id}>
           <td>{item.name}</td>
                      <td>{item.itemgroup}</td>
                      <td>{item.description}</td>
                      <td>{item.unit}</td>
                      <td>{item.brand}</td>
                      <td>{item.manufacturer}</td>
                      <td>{item.dimension}</td>
                      <td>{item.weight}</td>
                      <td>{item.sellingprice}</td>
                      <td>{item.costprice}</td>
                      <td>{item.openingstock}</td>
            <td>
           
            </td>
          </tr>
          
      }
  )}
  
</tbody>
</table>

   </div>
  
  
  </div>
    )
  }

export default Itemdashboard