
import axios from 'axios';
import React, { useState } from 'react'
import Sidebar from '../Sidebar';

const NewPayments = () => {

    const apiurl = '/api/newpayments'
    const[input, setInput]=useState({});
   
  
    const changeInput=(event)=>{
      setInput({
        ...input,[event.target.name]:event.target.value
      })
    }
  
  
  const buttonclick=()=>{
    
  
    axios.post(apiurl, input )
     
      .then((Response) => {
        console.log(Response.data);
        if (Response.data.status === 'success') {
          alert('New Payment is recieved');
        }
      })
      .catch((error) => {
        console.error('Error:', error);
        console.log('Request Details:', error.request);
        alert('Payment record creation is failed.');
      });
  };
    
    return (   
     
        <div className='d-flex' style={{ padding: '0', margin: '0' }}>
        <div className='container-fluid px-0 mx-0 col-2'> {' '} <Sidebar /> {' '} </div>
         
          
           <div className='container-fluid px-5 py-5 mx-0 d-flex  justify-content-center'>
            <div className="row">
              <div className="col-10">
              <h4 style={{fontWeight:'bold', fontSize:'large'}}>New Payment Record</h4>
              <hr style={{width:'140%', marginLeft:'-3rem'}}/>
  
              <form className="row g-3 my-4 py-5 px-5 " style={{fontSize:'small'}} encType="application/form-data" >
             
              
  
    <div className="col-md-6 col-sm-12">
      <label  className="form-label">Customer Name</label>
      <input type="text" name='customername' className="form-control" onChange={changeInput} id="customername"/>
      </div>
    
  
    <div className="col-md-6 ">
      <label className="form-label ">Amount</label>
      <input type="text" name='amount' className="form-control " onChange={changeInput} id="amount"/>
    </div>
   
    <div className="col-md-4">
      <label className="form-label">Payment ID</label>
      <input type="text" name='paymentid' className="form-control " onChange={changeInput} id="paymentid"/>
    </div>
    <div className="col-md-4">
      <label  className="form-label ">Payment Date</label>
      <input type="text" name='paymentdate' className="form-control " id="paymentdate" onChange={changeInput} placeholder=""/>
    </div>
    <div className="col-md-4 mb-3">
      <label className="form-label">Payment Mode</label>
      <input type="text" name='paymentmode' className="form-control " onChange={changeInput} id="paymentmode"/>
    </div>
    <div className="col-md-6">
      <label className="form-label">Deposit To</label>
      <input type="text" name='depositto' className="form-control" id="depositto" onChange={changeInput} placeholder=""/>
    </div>
    
    <div className="col col-12 my-4 d-flex justify-content-center ">
      <button type="button" className="btn btn-success " style={{width:'120px'}} onClick={buttonclick}>Add</button>
    </div>
  </form>
              </div>
            </div>
  
           </div>
  
          </div>
    )
  }

export default NewPayments